class cityController extends Controller {
    constructor() {
        super();

        let that = this;
        /*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
        搜索框的点击：
        -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
        require(['../../components/trend-search.min'],function(listSearch){
            listSearch.init(that,"newhouselist");
            listSearch.selectFun("icon-search");
        });
        /*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
        定位的一个实例
        -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
        new Location({
            businessType : "newTrend" ,
            cityApiUrl : this.apiUrl.common.getCityByLatLon ,
            identical : (position)=> {

            },
            investmentFlag:(Flag)=>{
                if (Flag.investment){
                    $('notopen-investment').show();
                }else {
                    $('notopen-investment').hide();
                }
            }
        }) ;
        /*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
        页面埋点
        -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
        require(['../../components/bigdata.min'],function(BigData){
            BigData.init(that);
            BigData.bigData({
                "pageName": "1113",
                "type": 1
            });
        });
    }
}
/*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
类的初始化
-----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
$(document).ready(function() {
    new cityController;
});